.chat-setting-controls {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .chat-control-fields {
    display: flex;
    flex-direction: column;

    .field-label,
    .input {
      color: #aaaaaa;
    }

    label {
      color: red;
      text-align: center;
    }
  }

  .chat-loading {
    height: 28px;
  }
}
