.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 16px;
  &.large {
    font-size: 30px;
  }

  .modal-close {
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 3px;
    transition: 0.3s all ease;
    &:hover {
      background-color: rgba(217, 230, 255, 0.2);
    }
  }
}
