.field-group {
  display: flex;
  gap: 8px;

  .input {
    width: -webkit-fill-available;
  }

  .suffix-color {
    color: #aaaaaa;
  }
}

.input-group {
  border-radius: 6px;
  overflow: hidden;
  display: flex;
  gap: 1px;
  margin-top: 12px;

  .icon {
    display: flex;
    margin: auto;
  }

  .rotate-90 {
    transform: rotate(90deg);
  }

  .rotate-180 {
    transform: rotate(180deg);
  }

  .rotate-270 {
    transform: rotate(270deg);
  }
}
