.collectible-setting-controls {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .collectible-control-fields {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .field-label,
    .input {
      color: #aaaaaa;
    }

    label {
      color: red;
    }
  }

  .collectible-loading {
    height: 28px;
  }
}
