.button-groups {
  display: flex;
  justify-content: space-between;
  padding: 2px;
  background-color: rgba(217, 230, 255, 0.1);
  border-radius: 8px;
  height: 32px;
  transition: 0.2s all ease;

  .button {
    border-radius: 4px;
  }
  &.large {
    height: 40px;
    padding: 4px;
    .button {
      border-radius: 6px;
    }
  }

  &.small {
    height: 28px;
    .button {
      border-radius: 4px;
    }
  }
}
