.text-style-modal {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  font-weight: 400;

  .action-buttons {
    display: flex;
    justify-content: stretch;
    gap: 12px;
    margin-top: 16px;
  }

  .modal-part {
    display: flex;
    flex-direction: column;
    gap: 12px;

    .label-color {
      color: #aaaaaa;
    }

    .suffix-color {
      color: #aaaaaa;
    }
  }

  .field-group {
    display: flex;
    gap: 8px;

    .input {
      width: -webkit-fill-available;
    }
  }
}
