.button {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  background-color: transparent;
  color: #aaaaaa;
  letter-spacing: 0.04em;
  padding: 10px;
  border-radius: 8px;
  line-height: 28px;
  border: none;
  font-family: 'RocGrotesk-Regular';

  &:hover,
  &.active {
    cursor: pointer;
    background-color: rgba(217, 230, 255, 0.2) !important;
    border: 0px solid rgba(217, 230, 255, 0.2) !important;
  }

  &.pilled {
    border-radius: 30px;
  }

  &.rounded {
    border-radius: 12px;
  }

  &.squared {
    border-radius: 0;
  }

  &.info {
    background-color: #e0f1e5 !important;
    border: 0px solid #e0f1e5 !important;
    color: #212121 !important;
    font-size: 14px;
    font-family: 'RocGrotesk-Medium';
  }

  &.primary {
    background-color: rgba(0, 72, 219, 1) !important;
    border: 0px solid rgba(0, 72, 219, 1) !important;
    color: #f9fafa !important;
    font-size: 14px;
    font-family: 'RocGrotesk-Medium';
    &:hover {
      background-color: rgba(0, 72, 219, 0.7) !important;
    }
  }

  &.warn {
    background-color: rgba(240, 68, 56, 1) !important;
    border: 0px solid rgba(240, 68, 56, 1) !important;
    color: #f9fafa !important;
    font-size: 14px;
    font-family: 'RocGrotesk-Medium';
    &:hover {
      background-color: rgba(240, 68, 56, 0.7) !important;
    }
  }

  &.secondary {
    background-color: rgba(217, 230, 255, 0.1) !important;
    border: 0px solid rgba(217, 230, 255, 0.1) !important;
    color: #aaaaaa !important;
    font-size: 14px;
    font-family: 'RocGrotesk-Medium';
    &:hover,
    &.active {
      color: #f9fafa !important;
      background-color: rgba(217, 230, 255, 0.2) !important;
      border: 0px solid rgba(217, 230, 255, 0.2) !important;
    }
  }

  &.outline {
    border-width: 1px;
    background-color: transparent !important;
  }

  &.filled {
    width: 100%;
  }

  &.menu-button {
    padding: 4px;
    &:hover {
      background-color: rgba(217, 230, 255, 0.1);
    }

    &.active {
      background-color: rgba(217, 230, 255, 0.1);
    }
  }
}
.color-select-button {
  width: 52px;
  border: 2px solid gray;
  border-radius: 6px !important;
  background-color: black;
}
