.editable-editor {
  display: flex;
  background-color: #aaa;
  padding-right: 10px;
  gap: 10px;

  .editor-section {
    width: 100%;
    height: calc(100vh - 20px);
    display: flex;
    flex-direction: column;
    margin: 10px 0;
    gap: 10px;
    font-family: 'Montserrat-Regular';
    .editor-header {
      height: 80px;
      background-color: #212121;
      padding: 20px;
      border-radius: 16px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .editor-header-buttons {
        display: flex;
        gap: 10px;

        .display-buttons {
          background-color: transparent;
          gap: 10px;
          padding: 0;
        }

        .changes-saved {
          color: green;
          font-size: 12px;
          display: flex;
          align-items: center;
          margin-right: 12px;
        }

        .header-tool-button-save {
          padding: 0 10px;
          height: 40px;
          width: 65px;
          background-color: rgba(217, 230, 255, 0.2);
          font-size: 16px;
          font-weight: 400;

          &.publish {
            font-size: 14px;
            font-weight: 500;
          }
        }
        .header-tool-button {
          padding: 0 10px;
          height: 40px;
          background-color: rgba(217, 230, 255, 0.2);
          font-size: 16px;
          font-weight: 400;

          &.publish {
            font-size: 14px;
            font-weight: 500;
          }
        }
      }
    }

    .editor-wrapper {
      position: relative;
      overflow: auto;
      height: 100%;
      border-radius: 10px;
      background-color: white;
      margin-right: auto;
      margin-left: auto;
      width: 100%;

      .editor-background {
        position: absolute;
        width: 100%;
        height: 100%;
      }

      ::-webkit-scrollbar {
        display: none;
      }

      &.desktop {
        margin-right: auto;
        margin-left: auto;

        ::-webkit-scrollbar {
          display: none;
        }
      }
      &.tablet {
        max-width: 820px;
        margin: 0 auto;
        ::-webkit-scrollbar {
          display: none;
        }
      }
      &.mobile {
        max-width: 390px;
        margin: 0 auto;

        ::-webkit-scrollbar {
          display: none;
        }
      }
    }
  }
}
