.file-uploader {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px dashed rgba(217, 230, 255, 0.3);
  border-radius: 12px;
  background-color: rgba(217, 230, 255, 0.1);
  cursor: pointer;
  padding: 20px;

  p {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    margin: 20px 0 0;
  }

  label {
    color: #aaaaaa;
    font-size: 12px;
    font-weight: 400;
  }
}

.image-preview {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: stretch;
  border: 1px solid rgba(217, 230, 255, 0.2);
  background-color: rgba(217, 230, 255, 0.1);
  border-radius: 12px;
  cursor: pointer;
  padding: 12px;

  img {
    max-width: 100%;
    margin: 0 auto;
  }

  label {
    color: #aaaaaa;
    font-size: 12px;
    font-weight: 400;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .action-buttons {
    display: flex;
    gap: 8px;
  }
}
