.react-page-plugins-content-footer {
  background-color: rgba(255, 255, 255, 1);
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  .logo-section {
    display: flex;
    align-items: center;
    gap: 15px;
    &:hover {
      cursor: pointer;
      opacity: 0.7;
      transition: 0.3s all ease;
    }

    img {
      height: 28px;
    }
  }

  label {
    color: #8e8e8e;
    font-size: 14px;
    pointer-events: none;
  }
}
