.select {
  $this: &;

  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(217, 230, 255, 0.1);
    border-radius: 6px;
    padding: 2px 12px;
    color: #f9fafa;
    width: 100%;
    height: 32px;
    gap: 12px;
  }

  input {
    padding-right: 0;
    border-right: none;
    &:hover {
      cursor: pointer;
    }
  }

  &__field {
    width: 100%;
    transition: all 0.2s ease;
    font-size: 14px;
  }

  &__chevron {
    display: flex;
    align-items: center;
    transition: all 0.2s ease;

    svg {
      transition: all 0.2s ease;
      opacity: 0.6;

      path {
        transition: all 0.2s ease;
      }
    }
  }

  &__rounded {
    .select__chevron {
      border-top-right-radius: 6px;
      border-bottom-right-radius: 6px;
    }

    .select__field {
      border-top-left-radius: 6px;
      border-bottom-left-radius: 6px;
    }

    input {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  &__bordered {
    .select__chevron {
      border-left: none;
    }

    .select__field {
      border-right: none;
    }
  }

  &__active,
  &:hover {
    .select__chevron {
      &__icon {
        opacity: 1;
      }
    }

    &.select__bordered {
      .select__chevron,
      .select__field,
      input {
      }
    }
  }

  &__active {
    .select__chevron {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__disabled {
    pointer-events: none;
  }
}

.transparent {
  background: transparent;
}
