.elements-view {
  overflow-y: auto;
  display: flex;
  flex-direction: column;

  .elements-row {
    display: flex;

    .react-page-toolbar-draggable {
      width: 50%;
      height: 110px;
      border: none;
      .elements-button {
        color: #aaaaaa;
        width: 100%;
        height: 100%;
        border: none;
        &.active {
          background-color: rgba(217, 230, 255, 0.1);
          color: #f9fafa;
        }
        &:hover {
          background-color: rgba(217, 230, 255, 0.1);
        }
      }
    }
  }
}
