.react-page-plugins-layout-background {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  padding: 12px;
  position: relative;
}

.react-page-plugins-layout-background > .react-page-row {
  position: relative;
}

.react-page-plugins-layout-background__backstretch {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.gradient-section {
  display: flex;
  flex-direction: column;
}

.angle-section {
  border-top: 1px solid rgba(217, 230, 255, 0.2);
  border-bottom: 1px solid rgba(217, 230, 255, 0.2);
  padding: 28px 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  font-size: 14px;
}

.angle-section .input {
  width: 100px;
}

.angle {
  gap: 20px;
}

.opacity-controls {
  display: flex;
  gap: 8px;
}

.action-buttons {
  display: flex;
  justify-content: stretch;
  gap: 12px;
  margin-top: 16px;
}

.format-part {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.label-color {
  color: #aaaaaa;
}

.suffix-color {
  color: #aaaaaa;
}

.background-part {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.field-group {
  display: flex;
  gap: 8px;
}

.input {
  width: -webkit-fill-available;
}

.color-label {
  font-size: 12px;
  font-weight: 700;
}

.rgb-colors {
  display: flex;
  gap: 8px;
}

.hex-color {
  flex: 2;
}

.rgb-color {
  flex: 1;
}

.color-buttons {
  display: flex;
  gap: 8px;
}

.two-colors {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
}

.color-button {
  border: 4px solid transparent;
  border-radius: 6px;
  height: 32px;
}

.color-button:active {
  border: 4px solid #f9fafa;
}

.color-button:hover {
  cursor: pointer;
}

.group-part {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.paralax-effect {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.css-53tbrc-MuiSlider-root {
  color: #5b5b5b;
}

.rcp-alpha {
  display: none;
}
