.sections-view {
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 500;
  gap: 32px;

  .plugin-image {
    border: 8px solid rgba(217, 230, 255, 0.1);
    border-radius: 12px;
    width: 100%;
    height: 150px;
  }

  /* The main container element */
  .Collapsible {
    background: #212121;
  }

  /* The content within the collaspable area  */
  .Collapsible__contentInner {
    padding: 20px 0 0;
    border: none;
    border-top: 0;

    p {
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  /* The link which when clicked opens the collapsable area */
  .Collapsible__trigger {
    display: block;
    font-weight: 600;
    font-size: 16px;
    text-decoration: none;
    position: relative;
    border: none;
    background: #212121;
    color: #aaaaaa;

    &:hover {
      cursor: pointer;
    }

    &:after {
      display: block;
      position: absolute;
      right: 0;
      top: 6px;
      content: '';
      height: 10px;
      width: 10px;
      border-color: #aaaaaa;
      margin-right: 12px;
      border-bottom: 2px solid currentColor;
      border-right: 2px solid currentColor;
      transform: rotate(45deg);
      transition: transform 300ms;
    }

    &.is-open {
      &:after {
        top: 9px;

        transition: all 0.3s ease;
        transform: rotateZ(225deg);
      }
    }

    &.is-disabled {
      opacity: 0.5;
      background-color: grey;
    }
  }

  .CustomTriggerCSS {
    background-color: lightcoral;
    transition: background-color 200ms ease;
  }

  .CustomTriggerCSS--open {
    background-color: darkslateblue;
  }

  .Collapsible__custom-sibling {
    padding: 5px;
    font-size: 12px;
    background-color: #cbb700;
    color: black;
  }
}
