.react-page-editable {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  min-height: 560px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -webkit-flex: 0 1 auto;
  -ms-flex: 0 1 auto;
  -webkit-box-flex: 0;
  flex: 0 1 auto;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;

  flex-direction: column;

  align-items: stretch;

  .react-page-cell-insert-new {
    cursor: cell;
    flex: 1;
    min-height: 100px;

    transition: all ease 0.4s;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    position: relative;
    border: 1px dashed rgba(0, 72, 219, 1);
    border-radius: 20px;
    background-color: rgba(224, 241, 229, 0.56);

    &.invisible {
      opacity: 0;
      &:hover {
        cursor: default;
      }
    }
  }

  .react-page-cell-insert-new-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    gap: 20px;
  }

  .react-page-cell-insert-new:hover,
  .react-page-cell-insert-new.hover {
    background-color: rgba(224, 241, 229, 1);
  }
}
