@import 'Item/index.css';
@import 'Views/index.scss';

.plugin-drawer-container {
  position: relative;
  height: calc(100vh - 20px);
  background-color: #212121;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  margin: 10px 0;
  display: flex;
  color: #fff;
  opacity: 1;
  font-family: 'RocGrotesk-Regular';
  .hide {
    opacity: 0;
  }

  .menu-side {
    width: 260px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 36px;
    padding: 20px;
    overflow-y: auto;
    transition: all 0.2s ease-in-out;

    .pulgin-drawer-tab-name {
      font-size: 20px;
      font-weight: 700;
    }

    .logo-image {
      width: 140px;
      height: 30px;
      margin: 15px auto 0px auto;
    }

    .menu-sections {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    .account-section {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: stretch;
      .account-button {
        align-self: flex-start;
        padding: 0;
        border-radius: 20px;
      }

      .circle-avatar {
        height: 32px;
        width: 32px;
        border-radius: 16px;
      }
    }

    .coming-soon-card {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 16px 18px;
      background: linear-gradient(90deg, #0048db, #9bb8f2);
      border-radius: 12px;
      gap: 10px;

      .coming-soon-mark {
        display: flex;
        align-items: center;
        height: 16px;
        border-color: 10px;
        font-size: 10px;
        font-weight: 500;
        border-radius: 8px;
        padding: 0 5px;

        background-color: rgba(217, 230, 255, 0.3);
      }
      .coming-description {
        line-height: 28px;
      }
    }
  }

  .extended-side {
    width: 260px;
    padding: 20px;
    border-left: 1px solid rgba(217, 230, 255, 0.2);
    overflow-y: auto;
    position: absolute;
    transition: all 0.2s ease-in-out;
    display: flex;
    flex-direction: column;
    gap: 32px;
    height: 100%;
    &.hide {
      pointer-events: none;
    }

    .back-button {
      display: flex;
      align-self: flex-start;
      align-items: center;
      padding: 0;
      gap: 6px;

      &:hover {
        background-color: transparent !important;
      }
    }
  }
}
