@import './typography.css';
@import './elements.css';
@import './fonts.css';

body {
  margin: 0px;

  color: #333333;
  font-size: 18px;
  line-height: 28px;
}

p {
  margin: 0;
}

pre {
  overflow-x: auto;
}

a {
  color: #c73036;
  font-family: Georgia, serif;
  text-decoration: underline;
}

a:hover {
  color: #333333;
  text-decoration: underline;
}
