@import './ModalHeader/index.scss';

.select-collectible-modal {
  .select-collectible-section {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 24px;
    padding: 32px 0 24px;
    border-bottom: 1px solid rgba(217, 230, 255, 0.2);
    border-top: 1px solid rgba(217, 230, 255, 0.2);

    .image-cards {
      display: flex;
      gap: 8px;

      .two-cards {
        display: flex;
        flex-direction: column;
        gap: 8px;
        width: 100%;

        .image-card {
          display: flex;
          overflow: hidden;
          position: relative;
          border: 4px solid transparent;
          border-radius: 16px;
          width: 100%;
          height: 144px;

          &.active {
            border: 4px solid #0048db;
          }

          &:hover {
            cursor: pointer;
          }

          .check-icon {
            position: absolute;
            top: 20px;
            left: 20px;
          }
        }
      }
    }
  }

  .link-section {
    display: flex;
    flex-direction: column;
    padding: 24px 0;
    gap: 24px;
  }
}

