.edit-section-modal {
  font-size: 14px;
  font-weight: 400;
  color: #aaaaaa;

  .group-part {
    display: flex;
    flex-direction: column;
    gap: 24px;
    .paralax-effect {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .css-53tbrc-MuiSlider-root {
      color: #5b5b5b;
    }
  }

  .gradient-section {
    display: flex;
    flex-direction: column;
    .angle-section {
      border-top: 1px solid rgba(217, 230, 255, 0.2);
      border-bottom: 1px solid rgba(217, 230, 255, 0.2);
      padding: 28px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .angle {
        gap: 20px;
      }
    }

    .opacity-section {
      .rcp-saturation {
        display: none;
      }
      .rcp-blue {
        display: none;
      }
      .rcp-hue {
        display: none;
      }

      .opacity-controls {
        display: flex;
        gap: 8px;
      }
    }
  }

  .action-buttons {
    display: flex;
    justify-content: stretch;
    gap: 12px;
    margin-top: 16px;
  }

  .format-part {
    display: flex;
    flex-direction: column;
    gap: 12px;

    .label-color {
      color: #aaaaaa;
    }

    .suffix-color {
      color: #aaaaaa;
    }
  }

  .background-part {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  .field-group {
    display: flex;
    gap: 8px;

    .input {
      width: -webkit-fill-available;
    }
  }

  .color-label {
    font-size: 12px;
    font-weight: 700;
  }

  .rgb-colors {
    display: flex;
    gap: 8px;

    .hex-color {
      flex: 2;
    }

    .rgb-color {
      flex: 1;
    }
  }

  .color-buttons {
    display: flex;
    gap: 8px;

    .two-colors {
      display: flex;
      flex-direction: column;
      gap: 8px;
      width: 100%;

      .color-button {
        border: 4px solid transparent;
        border-radius: 6px;
        height: 32px;

        &.active {
          border: 4px solid #f9fafa;
        }

        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  .rcp-light {
    --rcp-background: #ffffff00;
    --rcp-input-text: #111111;
    --rcp-input-border: rgba(0, 0, 0, 0.1);
    --rcp-input-label: #717171;
  }

  .rcp-dark {
    --rcp-background: #21212100;
    --rcp-input-text: #f3f3f3;
    --rcp-input-border: rgba(255, 255, 255, 0.1);
    --rcp-input-label: #999999;
  }

  .rcp {
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--rcp-background);
    border-radius: 10px;
  }

  .rcp-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;

    box-sizing: border-box;

    padding: 24px 0px 24px;
  }

  .rcp-saturation {
    position: relative;

    width: 100%;
    background-image: linear-gradient(transparent, black),
      linear-gradient(to right, white, transparent);
    border-radius: 8px;

    user-select: none;
  }

  .rcp-saturation-cursor {
    position: absolute;

    width: 32px;
    height: 32px;

    border: 8px solid #ffffff;
    border-radius: 50%;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
    box-sizing: border-box;

    transform: translate(-16px, -16px);
  }

  .rcp-hue {
    position: relative;

    width: 100%;
    height: 8px;

    background-image: linear-gradient(
      to right,
      rgb(255, 0, 0),
      rgb(255, 255, 0),
      rgb(0, 255, 0),
      rgb(0, 255, 255),
      rgb(0, 0, 255),
      rgb(255, 0, 255),
      rgb(255, 0, 0)
    );
    border-radius: 10px;

    user-select: none;
  }

  .rcp-hue-cursor {
    position: absolute;

    width: 24px;
    height: 24px;

    border: 6px solid #ffffff;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 0px 0.5px;
    box-sizing: border-box;

    transform: translate(-12px, -8px);
  }

  .rcp-alpha {
    position: relative;

    width: 100%;
    height: 12px;

    border-radius: 10px;

    user-select: none;
  }

  .rcp-alpha-cursor {
    position: absolute;

    width: 20px;
    height: 20px;

    border: 2px solid #ffffff;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 0px 0.5px;
    box-sizing: border-box;

    transform: translate(-10px, -4px);
  }

  .rcp-fields {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 10px;

    width: 100%;
  }

  .rcp-fields-element {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;

    width: 100%;
  }

  .hex-element {
    grid-row: 1;
  }

  .hex-element:nth-child(3n) {
    grid-column: 1 / -1;
  }

  .rcp-fields-element-input {
    width: 100%;

    font-size: 14px;
    font-weight: 600;

    color: var(--rcp-input-text);

    background: none;
    border: 2px solid;
    border-color: var(--rcp-input-border);
    border-radius: 5px;
    box-sizing: border-box;

    outline: none;

    padding: 10px;
  }

  .rcp-fields-element-label {
    font-size: 14px;
    font-weight: 600;

    color: var(--rcp-input-label);
    text-transform: uppercase;
  }
}

