.react-page-plugins-content-chat {
  height: 550px;
  display: flex;
  justify-content: stretch;
}
.channel-panel-area {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.str-chat.str-chat__theme-dark {
  border-radius: 15px !important;
}
.str-chat__message-input {
  border-bottom-right-radius: 15px !important;
  border-bottom-left-radius: 15px !important;
}
.react-page-plugins-connect-before {
  height: 400px;
  display: flex;
  justify-content: stretch;
  border-radius: 30px;
  background-color: #212121;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  font-family: RocGrotesk-Regular !important;
}
.welcome-text {
  color: white;
  font-size: 30px;
  font-family: 'RocGrotesk-Regular';
}
.chat-loading {
  height: 28px;
}
.chat-setting-controls {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .chat-control-fields {
    display: flex;
    flex-direction: column;

    .field-label,
    .input {
      color: #aaaaaa;
    }

    label {
      color: red;
      text-align: center;
    }
  }

  .chat-loading {
    height: 28px;
  }
}
