.react-page-plugins-content-collectible {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;

  .collectible-card-container {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    justify-content: center;
    align-items: center;
    background-color: rgba(224, 241, 229, 0.9);
    position: relative;
    border-radius: 12px;
    overflow: auto;
    gap: 10px;
    padding-bottom: 20px;
    transition: transform 0.3s ease-out;

    .card-image {
      position: relative;
      width: 100%;
      display: flex;
      justify-content: stretch;
      align-items: center;

      .circle-button-container {
        display: flex;
        position: absolute;
        right: 10px;
        bottom: 10px;
        width: 40px;
        height: 40px;
        border-radius: 30px;
        background-color: rgba(224, 241, 229, 0.5);
        justify-content: center;
        align-items: center;
        z-index: 1;
        &:hover {
          background-color: rgba(224, 241, 229, 0.7);
          cursor: pointer;
        }
      }

      .video-wrapper {
        z-index: 0;
        display: flex;
        justify-content: stretch;
      }

      img {
        width: 100%;
      }
    }

    label {
      font-size: 14px;
    }
  }
  &:hover {
    cursor: pointer;
  }
}
