.font-view {
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 500;
  gap: 32px;

  .preview-card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: rgba(217, 230, 255, 0.1);
    font-size: 14px;
    font-weight: 400;
    color: #f9fafa;
    border-radius: 16px;
    padding: 16px;
    gap: 16px;
    line-height: 21px;

    .preview-head {
      font-size: 24px;
      font-weight: 500;
    }

    .preview-button {
      padding: 10px 24px;
    }
  }
}
