.input {
  display: flex;
  flex-direction: column;

  label {
    margin-bottom: 8px;
    color: #aaaaaa;
    font-size: 12px;
  }
}

.input_container {
  display: flex;
  align-items: center;
  background-color: rgba(217, 230, 255, 0.1);
  border-radius: 6px;
  padding: 2px 12px;
  height: 32px;
  gap: 12px;

  &.large {
    height: 48px;
    padding: 10px 20px;
    border-radius: 12px;
    input {
      font-size: 16px;
    }
  }

  &.big {
    height: 40px;
    border-radius: 8px;
    padding: 8px 12px;

    input {
      font-size: 14px;
    }
  }

  input {
    width: 100%;
    padding: 8px 12px;
    transition: all 0.2s ease;
    border: none;
    background-color: transparent;
    color: #f9fafa;
    font-size: 14px;
    font-weight: 400;
    padding: 0;

    &.auto_size {
      transition: all 0.2s ease, width 0s;
    }

    &:focus-visible {
      border-color: green;
      outline: none;
    }
  }
}

.fill {
  width: 100%;
}

.squard {
  .input_container {
    border-radius: 0;
  }
}

