.menu-button {
  &.disabled {
    pointer-events: none;
    color: #aaaaaa;
  }
  .menu-item {
    display: flex;
    width: 100%;
    justify-content: stretch;
    align-items: center;
    transition: all 0.2s ease;
    gap: 16px;

    .menu-icon {
      background-color: rgba(217, 230, 255, 0.1);
      width: 40px;
      height: 40px;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .menu-title {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: center;
      pointer-events: none;
      flex: 1;
      label {
        display: flex;
        font-size: 16px;
      }
      span {
        line-height: 14px;
        display: flex;
      }
    }

    &:hover {
      cursor: pointer;
      .menu-title {
        color: #f9fafa;
      }
    }

    &.disabled {
      cursor: not-allowed;
      opacity: 0.6;
    }

    .option-menu {
      position: absolute;
      left: 250px;
      background: #2f2f2f;
      width: 160px;
      border-radius: 12px;
      font-size: 14px;
      padding: 8px;
      z-index: 10;
      .option-menu-item {
        display: flex;
        height: 40px;
        width: 100%;
        align-items: center;
        gap: 10px;
        padding-left: 10px;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}
