.tab-container {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(217, 230, 255, 0.2);
  margin-bottom: 24px;

  .tab-buttons {
    display: flex;
    padding: 0;
    gap: 24px;
    background-color: transparent;

    .tab-button {
      color: #aaaaaa;
      padding: 6px 0 8px 0;
      font-size: 16px;
      font-weight: 500;
      transition: all 0.2s ease;
      border-bottom: 2px solid transparent;

      &:hover {
        cursor: pointer;
        color: #f9fafa;
      }

      &.active {
        color: #f9fafa;
        border-bottom: 2px solid #f9fafa;
      }
    }
  }

  .close-button {
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 3px;
    transition: 0.3s all ease;
    &:hover {
      background-color: rgba(217, 230, 255, 0.2);
    }
  }
}
