.navigations-view {
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 500;
  gap: 32px;

  .navigation-image {
    display: flex;
    border: 8px solid rgba(217, 230, 255, 0.1);
    border-radius: 12px;
    width: 100%;
  }
}
